.StadiumDetail{
    min-height: 1000px;
    display: flex;
    
    
    flex-direction: column;
    
}
.StadiumDetail-comment-form{
    align-self: center;
    display: flex;
    flex-direction: column;
    margin-top: 25px;
}

.StadiumDetail-submit{
    align-self: center;
    margin-top: 10px;
}

.StadiumDetail-rating{
    align-self: center;
}

.StadiumDetail-divider{
    margin-top: 50px;
}

.StadiumDetail-comment-section{

    margin-top: 25px;
    margin-left: 10%;
    margin-right: 10%;
    
    padding: 25px;
    border-radius: 25px;
    margin-bottom: 50px;
}



.StadiumDetailArea{
    align-self: center;
    height: 100%;

    margin-top: 50px;
    
    width: 70%;
    display: flex;
    flex-direction: column;
    
    gap: 15px;
    align-items: center;

   
}

.Stadium-Detail-Textarea{
    width: 800px;
}




.Stadium-gallery{
    width: 800px;
    height: 400px;
   
}

.Stadium-Detail-Box{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

